@use '../abstracts/typography' as tp;

@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@400;500;700&display=swap');

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: tp.$ff-spartan;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}
